@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #e3edf7;
  color: #555;
  text-align: center;
  min-width: 300px;
}

a {
  color: inherit;
}

#root {
  height: 100vh;
  display: grid;
  place-items: center;
}

.container {
  display: flex;
  flex-direction: column;
  grid-gap: 30px;
  gap: 30px;
}

#timer-label span {
  position: absolute;
  top: 43px;
  border-bottom: 1px solid;
}

.block-button > div {
  box-shadow: 6px 6px 10px -1px rgb(0 0 0 / 15%),
    -6px -6px 10px -1px rgb(255 255 255 / 35%);
  border-radius: 10px;
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  overflow: hidden;
}

.block-button button {
  display: grid;
  place-items: center;
  border: unset;
  background-color: rgba(152, 50, 238, 0.05);
  padding: 10px;
  height: 50px;
  width: 60px;
  cursor: pointer;
}

button svg {
  color: #555;
}

.label span {
  font-size: 12px;
}

.block-button .block-btn {
  width: 130px;
  margin: auto;
}

.footer {
  position: absolute;
  bottom: 0;
  font-size: 12px;
  text-align: center;
  padding: 20px 15px;
}

.circle {
  position: relative;
  width: 200px;
  height: 200px;
  margin: auto;
  border-radius: 50%;
  padding: 20px;
  box-shadow: 6px 6px 10px -1px rgba(0, 0, 0, 0.15),
    -6px -6px 10px -1px rgba(255, 255, 255, 0.7);
}

.inner-shadow {
  height: 160px;
  width: 160px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: inset 4px 4px 6px -1px rgba(0, 0, 0, 0.2),
    inset -4px -4px 6px -1px rgba(255, 255, 255, 0.7),
    -0.5px -0.5px 0px rgba(255, 255, 255, 1),
    0.5px 0.5px 0px rgba(0, 0, 0, 0.15), 0px 12px 10px -10px rgba(0, 0, 0, 0.05);
}

#time-left {
  font-size: 45px;
  font-weight: 600;
  margin-top: 20px;
}

circle {
  fill: none;
  stroke: url(#GradientColor);
  stroke-width: 20px;
  stroke-dasharray: 565.2;
  /* stroke-dashoffset: 565.2; */
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-transform-origin: center;
          transform-origin: center;
  /*transition: stroke-dashoffset 2s linear;*/
}

.circle-animation {
  -webkit-animation: offset 60s linear infinite;
          animation: offset 60s linear infinite;
}

.circle svg {
  position: absolute;
  top: 0;
  left: 0;
}

@-webkit-keyframes offset {
  0% {
    stroke-dashoffset: 565.2;
  }
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes offset {
  0% {
    stroke-dashoffset: 565.2;
  }
  100% {
    stroke-dashoffset: 0;
  }
}

.paused {
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
}

